import supabase from "../lib/db";
export const createCommunity = async (userId, community) => {
    if (!userId || !community) {
        // eslint-disable-next-line no-console
        console.error('no userId or community');
        return null;
    }
    try {
        const { data, error } = await supabase
            .from('Community')
            .insert([
            Object.assign({}, community),
        ]).single();
        if (error)
            throw error;
        if (data)
            await supabase.from('Permission').insert([
                {
                    communityId: data.id,
                    type: 'owner',
                    userId,
                },
            ]);
        return data;
    }
    catch (err) {
        // eslint-disable-next-line no-console
        console.error(err);
        return null;
    }
};
export const updateCommunity = async (community) => {
    try {
        const { data, error } = await supabase
            .from('Community')
            .update(Object.assign({}, community))
            .match({ id: community.id })
            .single();
        if (error)
            throw error;
        // eslint-disable-next-line @typescript-eslint/no-unsafe-return
        return data;
    }
    catch (err) {
        // eslint-disable-next-line no-console
        console.error(err);
        return null;
    }
};
export const deleteCommunity = async (id) => {
    try {
        const { data, error } = await supabase
            .from('Community')
            .delete()
            .match({ id })
            .single();
        if (error)
            throw error;
        // eslint-disable-next-line @typescript-eslint/no-unsafe-return
        return data;
    }
    catch (err) {
        // eslint-disable-next-line no-console
        console.error(err);
        return null;
    }
};
export const getMyCommunites = async (userId) => {
    try {
        const { data, error } = await supabase
            .from('Permission')
            .select(`
        Community (
          isPublic,
          name,
          description,
          currency,
          color
        )
      `)
            .match({
            userId,
        });
        if (error)
            throw error;
        // @ts-ignore
        // eslint-disable-next-line @typescript-eslint/no-unsafe-return
        return data === null || data === void 0 ? void 0 : data.map((el) => el.Community);
    }
    catch (err) {
        // eslint-disable-next-line no-console
        console.error(err);
        return null;
    }
};
export const getCommunity = async (id) => {
    try {
        const { data, error } = await supabase
            .from('Community')
            .select()
            .match({ id })
            .single();
        if (error)
            throw error;
        return data;
    }
    catch (err) {
        // eslint-disable-next-line no-console
        console.error(err);
        return null;
    }
};
